@include font(GothamProBlack, GothamProBlack);
@include font(GothamPro, GothamPro);
@include font(GothamProLight, GothamProLight);
@include font(GothamProBold, GothamProBold);
@include font(Roboto-Bold, Roboto-Bold);
@include font(Roboto, Roboto);

body {
	font-family: "Open Sans", sans-serif;
	box-sizing: border-box;
	overflow: hidden;
	overflow-y: scroll; }
body::-webkit-scrollbar {
	width: 3px;
	background-color: #000; }
.center {
	max-width: 1168px;
	width: 100%;
	margin: auto;
	padding: 0 16px;
	@include r(1020) {
		max-width: 58.75rem; } }
@keyframes pulse {
	0% {
		transform: scale(0.5); }
	70% {
		transform: scale(0.55);
		box-shadow: 0 0 0 50px rgba(216, 0, 81, 0); }
	100% {
		transform: scale(0.5);
		box-shadow: 0 0 0 0 rgba(216, 0, 0, 0); } }
.hidden {
	opacity: 0; }
